import { Divider, Segmented, Table } from 'antd'
import React from 'react'
const columns =[
    {
        title: 'Thứ hạng',
        dataIndex: 'rank',
        key: 'rank',
        width: 100
    },
    {
        title: 'Thông tin',
        dataIndex: 'info',
        key: 'info'
    },
    {
        title: 'Doanh số',
        dataIndex: 'total',
        key: 'total',
        width: 200
    }
]
export default function CategoryRanking() {
    return (
        <>
            <Segmented
                size='large'
                options={['Theo doanh số']}
            />
            <Divider />
            <Table columns={columns}/>
        </>
    )
}
