import { CopyOutlined, DeleteOutlined, EditOutlined, EyeOutlined, WarningOutlined } from '@ant-design/icons';
import { Button, Input, message, Modal, Popconfirm, Table, Tabs, Tooltip } from 'antd'
import React, { useCallback, useEffect, useMemo, useState } from 'react'
import adminApi from '../../../apis/adminApi';
import helpers from '../../../helpers';
import VoucherModal from './VoucherModal/VoucherModal';
import moment from 'moment';
import AdminSearch from '../../../components/AdminSearch';

let initForm = {
    type: 1,
    code: null,
    apply: "",
    value: null,
    minSpend: null,
    total: null,
    start_time: new Date(),
    end_time: new Date(),
    amount: [],
    percentage: []
}
let isSearch = false;
export default function Voucher() {
    const [voucherDetails, setVoucherDetail] = useState({ visible: false, data: initForm });
    const [delModal, setDelModal] = useState({ visible: false, id: null })
    const [title, setTitle] = useState({ title: "Add new voucher", type: 'add' });
    const [voucherList, setVoucherList] = useState({ count: 0, rows: [] })
    const [page, setPage] = useState(1)
    const [searchOption, setSearchOption] = useState(null)
    const [searchValue, setSearchValue] = useState('')
    const [totalPage, setTotalPage] = useState(0)
    const [isLoading, setIsLoading] = useState(true);

    const columns = useMemo(() => [
        {
            title: 'Code',
            key: 'code',
            dataIndex: 'code',
        },
        {
            title: 'Type',
            key: 'type',
            dataIndex: 'apply',
            render: (type) => {
                if (!type) return "All"
                else return helpers.convertCategoryIdToString(2);

            }
        },
        {
            title: 'Discount',
            key: 'discount',
            render: (voucher) => {
                if (voucher.amount.length) return (<span style={{ color: 'red' }}>- $ {voucher.amount[0].amount}</span>)
                else if (voucher.percentage.length) return "- " + voucher.percentage[0].percent + "%"
            }
        },
        {
            title: 'Total',
            key: 'total',
            dataIndex: 'quantity',
        },
        {
            title: 'Used',
            key: 'used',
            dataIndex: 'used',
            render: (used) => used ? 0 : used
        },
        {
            title: 'Time',
            key: 'time',
            render: (voucher) => {
                return <>
                    {moment(voucher.start_time).format("YYYY-MM-DD-hh:mm A")}
                    <br />
                    {moment(voucher.end_time).format("YYYY-MM-DD-hh:mm A")}
                </>
            },
            filters: [
                {
                    text: 'Đang diễn ra',
                    value: 'London',
                },
                {
                    text: 'Sắp diễn ra',
                    value: 'New York',
                },
                {
                    text: 'Đã kết thúc',
                    value: 'New York',
                },
            ],
        },
        {
            title: 'Action',
            width: '150px',
            render: (voucher) => (
                <>
                    <Tooltip title="Edit" placement="left">
                        <EditOutlined
                            className="m-r-8 action-btn-product"
                            style={{ color: '#444' }}
                            onClick={() => {
                                setTitle({ title: "Voucher Details", type: 'edit' })
                                setVoucherDetail({ data: voucher, visible: true })
                            }}
                        />
                    </Tooltip>
                    <Tooltip title="Delete" placement="left">
                        <DeleteOutlined
                            onClick={() => setDelModal({ visible: true, id: voucher.id })}
                            className="m-r-8 action-btn-product"
                            style={{ color: 'red' }}
                        />
                    </Tooltip>
                    <Tooltip title="Copy to keyboard" placement="left">
                        <CopyOutlined
                            onClick={() => {
                                navigator.clipboard.writeText(voucher.code);
                                message.success("Voucher has been copied to keyboard !")
                            }}
                            className="m-r-8 action-btn-product"
                            style={{ color: 'blue' }}
                        />
                    </Tooltip>
                </>
            ),
        },
    ], [])
    const onCloaseVoucherModal = useCallback(
        () => {
            setVoucherDetail(prev => ({ ...prev, visible: false }))
        },
        [])
    const onDel = useCallback(async (id, voucherList) => {
        try {
            await adminApi.deleteVoucher(id);
            setDelModal({ visible: false, id: null })
            let data = voucherList.rows;
            let index = data.findIndex(item => item.id === id)
            if (index !== -1) {
                data.splice(index, 1);
            }
            setVoucherList(prev => ({ count: prev.count - 1, rows: [...data] }))
            message.success("The voucher has been deleted")
        } catch (error) {
            message.error(error.response.text)
        }
    }, [])
    const addNewVoucher = async (data) => {
        try {
            await adminApi.addNewVoucher(data);
            if (data.type === 1) {
                data.percentage = [{ percent: data.value }]
                data.amount = []
            } else if (data.type === 2) {
                data.amount = [{ amount: data.value }]
                data.percentage = []
            }
            data.used = 0;
            setVoucherList(prev => ({ count: prev.count + 1, rows: [data, ...prev.rows] }))
            message.success("Successful")
            setVoucherDetail({ visible: false, data: initForm })
        } catch (error) {
            message.error(error.response ? error.response.data : "Something went wrong !")
        }

    }
    const updateCurrentVoucher = async (data) => {
        try {
            console.log("153 Data: ", data, voucherList);
            await adminApi.updateVoucher(data)

            let voucherUpdate = voucherList.rows.find(item => item.id === data.id);
            Object.assign(voucherUpdate, data, { start_time: data.time[0], end_time: data.time[1] })
            // Clone từng voucher con trong mảng voucherList vì table antd nó 
            // sẽ ko render nếu object cũ
            let newVoucherList = voucherList.rows.map((item, index) => ({ ...item }))

            setVoucherList({ count: voucherList.count, rows: newVoucherList })
            message.success("Cập nhật thành công")
            setVoucherDetail({ visible: false, data: initForm })
        } catch (error) {
            console.log(error);
            message.error(error.response.data)
        }

    }
    const onSearch = useCallback(
        async (page, option, value, search) => {
            let voucherList = await adminApi.getVoucherListBy(page, option, value);
            isSearch = search;
            setVoucherList({ ...voucherList.data });
            setTotalPage(voucherList.data.count)
            setSearchOption(option);
            setSearchValue(value);
            setIsLoading(false);
            if (isSearch) setPage(page)
        },
        [],
    )

    useEffect(() => {
        let isSubscribe = true;
        const getAllVoucher = async () => {
            let voucherList = await onSearch(page, searchOption, searchValue, false);
            if (isSubscribe && voucherList) setVoucherList({ ...voucherList.data });
        }
        if (!isSearch) getAllVoucher();
        return () => {
            isSubscribe = false
            isSearch = false
        }
    }, [page])

    return (
        <div className='m-lr-10 m-t-10'>
            <div className='d-flex justify-content-between'>
                <h1>Danh Sách mã giảm giá</h1>
                <Button
                    type='primary'
                    size='large'
                    onClick={() => {
                        setTitle({ title: 'Add new voucher', type: 'add' })
                        setVoucherDetail(prev => ({
                            data: initForm,
                            visible: true
                        }))

                    }}
                >+ Add</Button>
            </div>
            <AdminSearch
                options={[{ text: "Tất Cả", id: 0 }, { text: "Code", id: 1 }]}
                onSearch={onSearch}
            />
            <Table
                columns={columns}
                dataSource={voucherList.rows ? [...voucherList.rows].map((item) => {
                    item.key = item.id;
                    // thêm thuộc tính key vào , nếu không nó báo lỗi
                    return item
                }) : []}
                pagination={{
                    showLessItems: true,
                    position: ['bottomCenter'],
                    position: ['bottomCenter'],
                    total: totalPage,
                    onChange: (p) => {
                        isSearch = false;
                        setPage(p)
                    }
                }}
            />
            {/* Delete modal */}
            <Modal
                title="Delete voucher"
                visible={delModal.visible}
                onOk={() => {
                    onDel(delModal.id, voucherList)
                }}
                onCancel={() => setDelModal({ visible: false, id: null })}
                okButtonProps={{ danger: true }}
                okText="Delete"
                cancelText="Cancel">
                <WarningOutlined style={{ fontSize: 28, color: '#F7B217' }} />
                <b> Are you sure that you want to permanently delete this voucher ?</b>
            </Modal>
            {/* Edit or Add new Voucher */}
            <VoucherModal
                visible={voucherDetails.visible}
                onClose={() => onCloaseVoucherModal()}
                title={title.title}
                handleData={title.type === 'edit' ? updateCurrentVoucher : addNewVoucher}
                data={voucherDetails.data}
                type={title.type}
            />
        </div>
    )
}
