import { Select, DatePicker, Button } from 'antd';
import moment from 'moment';
import React, { useRef, useState } from 'react'
import { Line } from 'react-chartjs-2'
import './index.scss'
const labels = ['01/01/2022', '01/02/2022', '01/03/2022', '20/04/2022', '01/05/2022', '01/06/2022', '01/07/2022'];
const data = {
    labels,
    datasets: [
        {
            label: 'Sales',
            data: [130, 117, 80, 153, 187, 120, 165],
            // borderColor: 'rgb(255, 99, 132)',
            type: 'line',
            borderColor: 'rgb(255, 100, 0)',
            backgroundColor: 'rgba(255, 99, 132, 0.5)',
            pointRadius: 6,
        },
        {
            label: 'Total',
            type: 'line',
            data: [100, 147, 90, 123, 87, 200, 185],
            borderColor: 'rgb(53, 162, 235)',
            backgroundColor: 'rgba(53, 162, 235, 0.5)',
            // pointStyle: 'rectRounded', reference : https://www.chartjs.org/docs/latest/api/#pointstyle
            pointRadius: 6,
            pointBorderColor: 'rgb(33, 5, 245,0.7)',
            pointBackgroundColor: 'rgb(0, 102, 255)',
            yAxisID: 'y1',

        },
    ],
};
const options = {
    responsive: true,
    interaction: {
        mode: 'index',
        intersect: true,
    },
    // stacked: false,
    plugins: {
        legend: {
            position: 'top',
            display: true
        },
        title: {
            display: true,
            text: 'Number of Order',
            font: {
                size: '20px'
            }
        },
        datalabels: {
            display: false, //default: true
            anchor: 'end',
            align: 'end',
            formatter: Math.round,
            font: {
                weight: 'bold',
                size: '18px'
            }
        }
    },
    scales: {
        y: {
            title: {
                display: true,
                text: 'Số đơn hàng',
                color: '#000',
                font: {
                    // family: 'Comic Sans MS',
                    size: 20,
                    // weight: 'bold',
                    lineHeight: 1.2,
                },
                padding: { top: 20, left: 0, right: 0, bottom: 0 },

            },
            min: 0,
            max: 200,
            // position: 'right',
        },
        y1: {
            title: {
                display: true,
                text: 'Doanh thu ( VND )',
                color: '#000',
                font: {
                    // family: 'Comic Sans MS',
                    size: 20,
                    // weight: 'bold',
                    lineHeight: 1.2,
                },
                padding: { top: 20, left: 0, right: 0, bottom: 0 }
            },
            min: 0,
            max: 300,
            position: 'right',
            grid: {
                display: false
            }
        }
        ,
        x: {
            title: {
                display: true,
                text: 'Month',
                color: '#000',
                font: {
                    // family: 'Comic Sans MS',
                    size: 20,
                    // weight: 'bold',
                    lineHeight: 1.2,
                },
                padding: { top: 20, left: 0, right: 0, bottom: 0 }
            },
            type: 'time',
            time: {
                parser: 'DD/MM/YYYY',
                unit: 'month',
                displayFormats: {
                    month: 'MM / YY'
                },
                tooltipFormat: 'DD-MM-YYYY'
            }
        },
    }

};
const { RangePicker } = DatePicker;
const { Option } = Select;
export default function LineChart() {
    let datePickerRef = useRef(null)
    let [isOpen, setIsOpen] = useState(false)
    return (
        <div className="bar_chart_option">
            <Select
                // defaultValue="lucy"
                style={{
                    width: 270,
                }}
                // loading
                onChange={(e) => {
                    console.log("first")
                    if(e==='custom'){
                        setIsOpen(true)
                        datePickerRef.current.style.display = 'inline'
                        console.log(datePickerRef.current.style);
                    }

                }}
            >
                <Option value={[moment().format('DD/MM/YYYY')]}>Hôm nay ({moment().format('DD/MM/YYYY')})</Option>
                <Option value={[moment().subtract(7,'d').format('DD/MM/YYYY'),moment().format('DD/MM/YYYY')]}>7 Ngày ({moment().subtract(7,'d').format('DD/MM/YYYY')} - {moment().format('DD/MM/YYYY')})</Option>
                <Option value={[moment().subtract(30,'d').format('DD/MM/YYYY'),moment().format('DD/MM/YYYY')]}>30 Ngày ({moment().subtract(30,'d').format('DD/MM/YYYY')} - {moment().format('DD/MM/YYYY')})</Option>
                <Option value="custom"
                >Tùy chỉnh</Option>
            </Select>
            <div className="datepicker" ref={datePickerRef}>
                <RangePicker open={isOpen}
                    // onOk={() => console.log(123)}  
                    onChange={(e) => console.log(e)}
                    renderExtraFooter={(e) => <Button className="text-right" type="primary" onClick={() => {
                        datePickerRef.current.style.display = 'none'
                        setIsOpen(false)
                    }}>Apply</Button>}
                />
            </div>

            <Line options={options} data={data} size="large" />
        </div>
    )
}
