import React, { useState } from 'react'
import { Col, Row, message, Tooltip, Segmented, Divider, Table } from 'antd';
import LineChart from '../../../components/LineChart/LineChart';
import './index.scss'
import { QuestionCircleOutlined } from '@ant-design/icons';
import Ranking from './Ranking/Ranking';
import CategoryRanking from './CategoryRanking/CategoryRanking';
const toolTipText = {
  total: 'Tổng giá trị của các đơn hàng đã xác nhận (bao gồm không thanh toán khi nhận hàng và xác nhận thanh toán khi nhận hàng) trong khoảng thời gian đã chọn, bao gồm cả các đơn hàng đã hủy và trả hàng / hoàn tiền.',
  orders: 'Tổng số đơn đặt hàng đã xác nhận trong khoảng thời gian đã chọn, bao gồm cả đơn đặt hàng đã hủy và trả lại / hoàn tiền.',
  views: 'Tổng số truy cập xem sản phẩm',
  totalOrder: 'Giá trị trung bình của mỗi đơn hàng trong khoảng thời gian đã chọn, được tính bằng tổng doanh số chia tổng đơn hàng.'
}


export default function Analysis() {
  const [options, setOptions] = useState({
    total: true,
    orders: false,
    views: false,
    totalOrder: false
  })
  const chooseOption = (key) => {
    // if(options.total ||)
    // console.log(key);
    let optionsUpdate = { ...options, [key]: !options[key] }
    let countTrue = 0
    for (let key in optionsUpdate) {
      if (optionsUpdate[key]) {
        countTrue++;
      }
    }
    if (countTrue !== 0) {
      setOptions(optionsUpdate)
    } else {
      message.warning("Vui lòng chọn tối thiểu 1 chỉ tiêu")
      return;
    }

  }
  return (
    <Row className="p-32 analysis" gutter={[8, 16]}>
      <Col span={24}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-10">
          <h2>Khung thời gian</h2>
        </div>
      </Col>
      <Col span={24} xl={6}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30"
          style={{ borderTop: options.total ? '4px solid rgb(88, 183, 241)' : '' }}
          onClick={() => chooseOption('total')}
        >
          <h2> Doanh số <Tooltip title={toolTipText.total}><QuestionCircleOutlined className='icon' /></Tooltip>
          </h2>
        </div>
      </Col>
      <Col span={24} xl={6}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30"
          style={{ borderTop: options.orders ? '4px solid rgb(255, 107, 69)' : '' }}
          onClick={() => chooseOption('orders')}
        >
          <h2>Đơn hàng <Tooltip title={toolTipText.orders}><QuestionCircleOutlined className='icon' /></Tooltip></h2>
        </div>
      </Col>
      <Col span={24} xl={6}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30"
          style={{ borderTop: options.views ? '4px solid rgb(38, 170, 153)' : '' }}
          onClick={() => chooseOption('views')}
        >
          <h2> Lượt xem <Tooltip title={toolTipText.views}><QuestionCircleOutlined className='icon' /></Tooltip></h2>
        </div>
      </Col>
      <Col span={24} xl={6}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30"
          style={{ borderTop: options.ratio ? '4px solid rgb(38, 115, 221)' : '' }}
          onClick={() => chooseOption('ratio')}
        >
          <h2> Doanh số trên đơn hàng <Tooltip title={toolTipText.totalOrder}><QuestionCircleOutlined className='icon' /></Tooltip></h2>
        </div>
      </Col>
      <Col span={24}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30">
          <LineChart />
        </div>
      </Col>
      <Col span={24} xl={15}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30">
          <Ranking/>
        </div>
      </Col>
      <Col span={24} xl={9}>
        <div className="bg-white p-lr-12 bor-rad-8 p-tb-30">
          <h1>Thứ hạng theo thể loại</h1>
          <CategoryRanking/>
        </div>
      </Col>
    </Row >
  )
}
