import { QuestionCircleOutlined } from '@ant-design/icons'
import { Divider, Segmented, Table, Tooltip } from 'antd'
import React, { useState } from 'react'
const rankingTooltipText = {
    total: '5 sản phẩm có tổng doanh số cao nhất (tính trên các đơn hàng đã được xác nhận) trong khoảng thời gian đã chọn.',
    quantity: '5 sản phẩm bán chạy nhất của Shop theo tổng số lượng sản phẩm đã xác nhận trong khoảng thời gian được chọn.',
    views: 'Top 5 sản phẩm của Shop bạn có nhiều lượt xem nhất trong khoảng thời gian đã chọn.',
    ratio: 'Top 5 sản phẩm có tỉ lệ đặt hàng và đơn đã xác nhận cao nhất (số khách có đơn đã xác nhận chia cho số lượt truy cập)'
}
const segmentedOptions = [
    {
        label: (
            <p>Theo doanh số <Tooltip title={rankingTooltipText.total}><QuestionCircleOutlined className='icon' />
            </Tooltip>
            </p>),
        value: 1
    },
    {
        label: (
            <p>Theo số lượng bán <Tooltip title={rankingTooltipText.quantity}><QuestionCircleOutlined className='icon' />
            </Tooltip>
            </p>),
        value: 2
    },
    {
        label: (
            <p>Theo lượt xem <Tooltip title={rankingTooltipText.views}><QuestionCircleOutlined className='icon' />
            </Tooltip>
            </p>),
        value: 3
    }, {
        label: (
            <p>Theo tỷ lệ chuyển đổi <Tooltip title={rankingTooltipText.ratio}><QuestionCircleOutlined className='icon' />
            </Tooltip>
            </p>),
        value: 4
    }
]

export default function Ranking() {
    const [columns, setColumns] = useState([
        {
            title: 'Thứ hạng',
            dataIndex: 'rank',
            key: 'rank',
            width: 100
        },
        {
            title: 'Thông tin',
            dataIndex: 'info',
            key: 'info'
        },
        {
            title: 'Doanh số',
            dataIndex: 'total',
            key: 'total',
            width: 200
        },
    ])
    const changeColumnsTitle = (key) => {
        let newColumns = [...columns];
        if (key === 1) {
            newColumns[2] = { ...newColumns[2], title: 'Theo Doanh Số' }
        } else if (key === 2) {
            newColumns[2] = { ...newColumns[2], title: 'Theo Số Lượng' }
        } else if (key === 3) {
            newColumns[2] = { ...newColumns[2], title: 'Theo Lượt Xem' }
        } else {
            newColumns[2] = { ...newColumns[2], title: 'Theo Tỷ Lệ' }
        }
        setColumns([...newColumns])
    }
    return (
        <>
            <h1>Thứ hạng sản phẩm</h1>
            <Segmented
                size='large'
                options={segmentedOptions}
                onChange={(value) => changeColumnsTitle(value)}
            />
            <Divider />
            <Table columns={columns} />
        </>
    )
}
